const colors = ['red', 'yellow', 'blue', 'green']

const levels = {
  easy: ['red', 'yellow', 'blue'], // –õ–µ–≥–∫–∏–π —É—Ä–æ–≤–µ–Ω—å (3 —Ü–≤–µ—Ç–∞)
  medium: ['red', 'yellow', 'blue', 'green', 'orange'], // –°—Ä–µ–¥–Ω–∏–π —É—Ä–æ–≤–µ–Ω—å (5 —Ü–≤–µ—Ç–æ–≤)
  hard: ['red', 'yellow', 'blue', 'green', 'orange', 'purple', 'pink', 'brown', 'gray', 'black'], // –°–ª–æ–∂–Ω—ã–π —É—Ä–æ–≤–µ–Ω—å (10 —Ü–≤–µ—Ç–æ–≤)
}

const colorTranslations = {
  red: '–∫—Ä–∞—Å–Ω—ã–π',
  yellow: '–∂–µ–ª—Ç—ã–π',
  blue: '—Å–∏–Ω–∏–π',
  green: '–∑–µ–ª–µ–Ω—ã–π',
  orange: '–æ—Ä–∞–Ω–∂–µ–≤—ã–π',
  purple: '—Ñ–∏–æ–ª–µ—Ç–æ–≤—ã–π',
  pink: '—Ä–æ–∑–æ–≤—ã–π',
  brown: '–∫–æ—Ä–∏—á–Ω–µ–≤—ã–π',
  gray: '—Å–µ—Ä—ã–π',
  black: '—á–µ—Ä–Ω—ã–π',
}

// –ì–µ–Ω–µ—Ä–∞—Ü–∏—è 15 –≤–∞—Ä–∏–∞–Ω—Ç–æ–≤ –∫–∞—Ä—Ç–æ—á–µ–∫
const generateCards = (level) => {
  const colors = levels[level]
  const cards = [];
  let previousTextColor = null // –•—Ä–∞–Ω–∏–º –ø—Ä–µ–¥—ã–¥—É—â–∏–π —Ç–µ–∫—Å—Ç

  for (let i = 0; i < 15; i++) {
    let textColor
    do {
      textColor = colors[Math.floor(Math.random() * colors.length)]
    } while (textColor === previousTextColor); // –£–±–µ–¥–∏–º—Å—è, —á—Ç–æ —Ç–µ–∫—Å—Ç –Ω–µ –ø–æ–≤—Ç–æ—Ä—è–µ—Ç—Å—è

    let backgroundColor
    do {
      backgroundColor = colors[Math.floor(Math.random() * colors.length)]
    } while (backgroundColor === textColor) // –£–±–µ–¥–∏–º—Å—è, —á—Ç–æ —Ü–≤–µ—Ç —Ñ–æ–Ω–∞ –æ—Ç–ª–∏—á–∞–µ—Ç—Å—è –æ—Ç —Ç–µ–∫—Å—Ç–∞

    cards.push({
      textColor,
      backgroundColor,
      text: colorTranslations[textColor], // –î–æ–±–∞–≤–ª—è–µ–º —Ç–µ–∫—Å—Ç –Ω–∞ —Ä—É—Å—Å–∫–æ–º
    });

    previousTextColor = textColor // –û–±–Ω–æ–≤–ª—è–µ–º –ø—Ä–µ–¥—ã–¥—É—â–∏–π —Ç–µ–∫—Å—Ç
  }

  return cards
}

export default {
  namespaced: true,
  state: {
    cards: generateCards('easy'), // –°–ø–∏—Å–æ–∫ –∫–∞—Ä—Ç–æ—á–µ–∫
    currentCardIndex: 0, // –ò–Ω–¥–µ–∫—Å —Ç–µ–∫—É—â–µ–π –∫–∞—Ä—Ç–æ—á–∫–∏
    selectedColor: null, // –í—ã–±—Ä–∞–Ω–Ω—ã–π —Ü–≤–µ—Ç
    isCorrect: null, // –ü—Ä–∞–≤–∏–ª—å–Ω—ã–π –ª–∏ –≤—ã–±–æ—Ä
    isFinished: false, // –ó–∞–∫–æ–Ω—á–µ–Ω –ª–∏ —Ç—Ä–µ–Ω–∞–∂–µ—Ä
    currentLevel: 'easy', // –¢–µ–∫—É—â–∏–π —É—Ä–æ–≤–µ–Ω—å —Å–ª–æ–∂–Ω–æ—Å—Ç–∏
    startGame: false,
  },
  getters: {
  },
  mutations: {
    setSelectedColor(state, color) {
      state.selectedColor = color
      console.log('state.selectedColor :>> ', state.selectedColor);
    },
    checkAnswer(state) {
      const currentCard = state.cards[state.currentCardIndex]
      state.isCorrect = state.selectedColor === currentCard.textColor
      console.log('state.isCorrect :>> ', state.isCorrect);
    },
    nextCard(state) {
      console.log('nextCard :>> ');
      if (state.currentCardIndex < state.cards.length - 1) {
        state.currentCardIndex++
        state.selectedColor = null
        state.isCorrect = null
      } else {
        state.isFinished = true
      }
    },
    resetGame(state) {
      state.cards = generateCards(state.currentLevel)
      state.currentCardIndex = 0
      state.selectedColor = null
      state.isCorrect = null
      state.isFinished = false
      state.startGame = false

    },
    setLevel(state, level) {
      state.currentLevel = level
      state.cards = generateCards(level) // –ì–µ–Ω–µ—Ä–∞—Ü–∏—è –∫–∞—Ä—Ç–æ—á–µ–∫ –¥–ª—è –Ω–æ–≤–æ–≥–æ —É—Ä–æ–≤–Ω—è
      state.currentCardIndex = 0 // –°–±—Ä–æ—Å –∏–Ω–¥–µ–∫—Å–∞ –∫–∞—Ä—Ç–æ—á–∫–∏
      state.selectedColor = null // –°–±—Ä–æ—Å –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ —Ü–≤–µ—Ç–∞
      state.isCorrect = null // –°–±—Ä–æ—Å —Å—Ç–∞—Ç—É—Å–∞ –ø—Ä–∞–≤–∏–ª—å–Ω–æ—Å—Ç–∏
      state.isFinished = false // –°–±—Ä–æ—Å —Å—Ç–∞—Ç—É—Å–∞ –∑–∞–≤–µ—Ä—à–µ–Ω–∏—è
      state.startGame = true
    },
  },
  actions: {
    selectColor({ commit, state }, color) {
      console.log('color :>> ', color);
      commit('setSelectedColor', color)
      commit('checkAnswer')
      if (state.isCorrect) {
        setTimeout(() => {
          commit('nextCard')
        }, 1000); // –ó–∞–¥–µ—Ä–∂–∫–∞ –ø–µ—Ä–µ–¥ –ø–µ—Ä–µ—Ö–æ–¥–æ–º –∫ —Å–ª–µ–¥—É—é—â–µ–π –∫–∞—Ä—Ç–æ—á–∫–µ
      }
    },
    setLevel({ commit }, level) {
      commit('setLevel', level);
    },
  },
}